<template>
    <div class="container">
        <!-- 这里放置你想要的内容 -->
<!--        <div id="clustrmaps-container" class="w-100 overflow-hidden"></div>-->
    </div>
</template>

<script>
export default {
    name: "LogView",
    mounted() {
        // 获取 clustrmaps-container 元素
        const container = document.getElementById('clustrmaps-container');

        // 动态创建 ClustrMaps 脚本
        const script = document.createElement('script');
        script.type = 'text/javascript';
        script.id = 'clustrmaps';
        script.src = '//clustrmaps.com/map_v2.js?d=qPKY5xtavrblNcCDoi36pH6CG3lbTMz_7YzLimXcyT8&cl=ffffff&w=a';

        // 将脚本添加到 container 中而不是直接添加到 body
        container.appendChild(script);  // 将脚本添加到指定的容器中
    }
}
</script>

